export function _debounce(f, ms) {
  let timer = null;
  return function () {
    clearTimeout(timer);
    timer = setTimeout(() => { f.apply(this, arguments) }, ms);
  };
}

export function _throttle(f, ms) {
  let isRun = true
  return function () {
    if (isRun) {
      isRun = false;
      f.apply(this, arguments);
      setTimeout(() => {
        isRun = true;
      }, ms);
    }
  }
  // 并未做，保留参数计时结束回调功能
}

export const login_check = {
  password: /^[\S]{6,12}$/,
  email: /^.+@(.+\.)+.+$/
}

export function formatDate(date, fmt) {
  if (/(y+)/.test(fmt)) {
    fmt = fmt.replace(RegExp.$1, (date.getFullYear() + '').slice(4 - RegExp.$1.length));
  }
  let o = {
    'M+': date.getMonth() + 1,
    'd+': date.getDate(),
    'h+': date.getHours(),
    'm+': date.getMinutes(),
    's+': date.getSeconds()
  };
  for (let k in o) {
    if (new RegExp(`(${k})`).test(fmt)) {
      let str = o[k] + '';
      fmt = fmt.replace(RegExp.$1, (RegExp.$1.length === 1) ? str : padLeftZero(str));
    }
  }
  return fmt;
};

function padLeftZero(str) {
  return ('00' + str).slice(str.length);
};

